<template>
    <div class="flex items-center w-full absolute bg-gray-700/75 left-0 bottom-0 z-10">
        <button class="flex flex-1 justify-center py-4" @click="previous"> <i class="icon icon-backward"></i> </button>
        <button class="flex flex-1 justify-center py-4" @click="next"> <i class="icon icon-forward"></i> </button>
    </div>
    <div class="flex h-[80vh] w-full">
        <div class="flex justify-center items-center w-full h-full" v-show="!slide" @click="slide=(slide+1)%2">
            <p class="w-full font-bold text-2xl"> {{ showWords[index].Español }} </p>
        </div>
        <div class="flex flex-col justify-center items-center w-full h-full" v-show="slide" @click="slide=(slide+1)%2">
            <p class="w-full font-bold text-2xl"> {{ showWords[index].Traduccion }} </p>
            <img v-if="words[index].Imagen2 != ''" :src="showWords[index].Imagen2" class="w-1/3 mx-auto mt-10">
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
    name: 'oneSlide',
    data (){
        return {
            index: 0,
            slide: null,
            slide_default: null,
            showWords: this.words
        }
    },
    computed: {
        ...mapState(['words', 'show']),
    },
    methods: {
        next (){
            if (this.show[0] == 2){
                if (Date.now()%2) this.slide = 1;
                else this.slide = 0;
            }
            else this.slide = this.slide_default;
            this.index = (this.index < this.showWords.length-1) ? this.index += 1 : 0;
        },
        previous (){
            if (this.show[0] == 2){
                if (Date.now()%2) this.slide = 1;
                else this.slide = 0;
            }
            else this.slide = this.slide_default;
            this.index = (this.index == 0) ? this.showWords.length-1 : this.index-1;
        }
    },
    beforeMount (){
        if (this.show[1]!='*') this.showWords = this.words.filter( w => w.Categoria == this.show[1]);
        else this.showWords = this.words;
    },
    mounted (){
        if (this.show[0] == 1) this.slide_default = 1; // Mostrar la traducción primero
        else if (this.show[0] == 2){
            if (Date.now()%2) this.slide = 1;
            else this.slide = 0;
        }
        else this.slide_default = 0; // Mostrar en español
        this.slide = this.slide_default;
    }
}
</script>
